<script>
import axios from "axios";
import AddOrUpdate from "./add-or-update";
export default {
  data() {
    return {
      searchForm: {
        key: "",
		name:"",
      },
      form: {},
      dataList: [],
      OrgList:[],
      pageIndex: 1,
      pageSize: 10,
      totalPage: 0,
      dataListLoading: false,
      dataListSelections: [],
      showFlag: true,
      sfshVisiable: false,
      shForm: {},
      chartVisiable: false,
      chartVisiable1: false,
      chartVisiable2: false,
      chartVisiable3: false,
      chartVisiable4: false,
      chartVisiable5: false,
      addOrUpdateFlag: false,
      layouts: ["total", "prev", "pager", "next", "sizes", "jumper"],
    };
  },
  created() {
    this.init();
    this.getDataList();
    this.contentStyleChange();
  },
  mounted() {},
  filters: {
    htmlfilter: function (val) {
      return val.replace(/<[^>]*>/g).replace(/undefined/g, "");
    },
  },
  components: {
    AddOrUpdate,
  },
  methods: {
    contentStyleChange() {
      this.contentPageStyleChange();
    },
    // 分页
    contentPageStyleChange() {
      let arr = [];

      // if(this.contents.pageTotal) arr.push('total')
      // if(this.contents.pageSizes) arr.push('sizes')
      // if(this.contents.pagePrevNext){
      //   arr.push('prev')
      //   if(this.contents.pagePager) arr.push('pager')
      //   arr.push('next')
      // }
      // if(this.contents.pageJumper) arr.push('jumper')
      // this.layouts = arr.join()
      // this.contents.pageEachNum = 10
    },

    init() {
      this.sfshOptions = "是,否,待审核".split(",");
    },
    search() {
      this.pageIndex = 1;
      this.getDataList();
    },

    // 获取数据列表
    getDataList() {
      this.dataListLoading = true;
      let params = {
        page: this.pageIndex,
        size: this.pageSize,
        position: {},
      };
      if (this.searchForm.name) {
        let data = this.dataList.find((item) => {
          return item.name == this.searchForm.name;
        });
        
        if (data&&data.id) {
          let id=data.id
          params['position'] = { id:id, name: this.searchForm.name,createtime:null,departmentid: this.OrgList.find(item=>{
            return item.id == id
          }).departmentid };
        } else {
          params.position == undefined;
        }
      } else {
        params.position == undefined;
      }
      //   if(this.searchForm.sfsh!='' && this.searchForm.sfsh!=undefined){
      //     params['sfsh'] = this.searchForm.sfsh
      //   }
      //    if(this.searchForm.yuangongxingming!='' && this.searchForm.yuangongxingming!=undefined){
      //     params['yuangongxingming'] = '%' + this.searchForm.yuangongxingming + '%'
      //   }
      //   if(this.searchForm.sfsh!='' && this.searchForm.sfsh!=undefined){
      //     params['sfsh'] = this.searchForm.sfsh
      //   }
      //    if(this.searchForm.bumen!='' && this.searchForm.bumen!=undefined){
      //     params['bumen'] = '%' + this.searchForm.bumen + '%'
      //   }
      //   if(this.searchForm.sfsh!='' && this.searchForm.sfsh!=undefined){
      //     params['sfsh'] = this.searchForm.sfsh
      //   }
      //   if(this.searchForm.sfsh!='' && this.searchForm.sfsh!=undefined){
      //     params['sfsh'] = this.searchForm.sfsh
      //   }
      //   if(this.searchForm.sfsh!='' && this.searchForm.sfsh!=undefined){
      //     params['sfsh'] = this.searchForm.sfsh
      //   }
      //   if(this.searchForm.sfsh!='' && this.searchForm.sfsh!=undefined){
      //     params['sfsh'] = this.searchForm.sfsh
      //   }
      //   if(this.searchForm.sfsh!='' && this.searchForm.sfsh!=undefined){
      //     params['sfsh'] = this.searchForm.sfsh
      //   }
      //   if(this.searchForm.sfsh!='' && this.searchForm.sfsh!=undefined){
      //     params['sfsh'] = this.searchForm.sfsh
      //   }
      //   if(this.searchForm.sfsh!='' && this.searchForm.sfsh!=undefined){
      //     params['sfsh'] = this.searchForm.sfsh
      //   }
      //   if(this.searchForm.sfsh!='' && this.searchForm.sfsh!=undefined){
      //     params['sfsh'] = this.searchForm.sfsh
      //   }
      this.$http({
        url: "department/queryAll",
        method: "post",
        data: {},
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.OrgList = data.data;
        } else {
          this.OrgList = [];
        }
      });
      this.$http({
        url: "position/queryPage",
        method: "post",
        data: params,
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.dataList = data.data.list;
          this.totalPage = data.data.total;
        } else {
          this.dataList = [];
          this.totalPage = 0;
        }
        this.dataListLoading = false;
      });
    },
    // 每页数
    sizeChangeHandle(val) {
      this.pageSize = val;
      this.pageIndex = 1;
      this.getDataList();
    },
    // 当前页
    currentChangeHandle(val) {
      this.pageIndex = val;
      this.getDataList();
    },
    // 多选
    selectionChangeHandler(val) {
      this.dataListSelections = val;
    },
    // 添加/修改
    addOrUpdateHandler(id, type) {
      this.showFlag = false;
      this.addOrUpdateFlag = true;
      this.crossAddOrUpdateFlag = false;
      if (type != "info") {
        type = "else";
      }
      this.$nextTick(() => {
        this.$refs.addOrUpdate.init(id, type);
      });
    },
    // 审核窗口
    shDialog(row) {
      this.sfshVisiable = !this.sfshVisiable;
      if (row) {
        this.shForm = {
          yuangonggonghao: row.yuangonggonghao,
          yuangongxingming: row.yuangongxingming,
          bumen: row.bumen,
          gangwei: row.gangwei,
          xiaojiabianhao: row.xiaojiabianhao,
          tupian: row.tupian,
          xiaojiashijian: row.xiaojiashijian,
          xiaojiabeizhu: row.xiaojiabeizhu,
          sfsh: row.sfsh,
          shhf: row.shhf,
          id: row.id,
        };
      }
    },
    // 审核
    shHandler() {
      this.$confirm(`确定操作?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.$http({
          url: "position/update",
          method: "post",
          data: this.shForm,
        }).then(({ data }) => {
          if (data && data.code === 0) {
            this.$message({
              message: "操作成功",
              type: "success",
              duration: 1500,
              onClose: () => {
                this.getDataList();
                this.shDialog();
              },
            });
          } else {
            this.$message.error(data.msg);
          }
        });
      });
    },
    // 下载
    download(file) {
      window.open(`${file}`);
    },
    // 删除
    deleteHandler(id) {
      var ids = id
        ? [Number(id)]
        : this.dataListSelections.map((item) => {
            return Number(item.id);
          });
      this.$confirm(`确定进行[${id ? "删除" : "批量删除"}]操作?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.$http({
          url: "position/delete?id="+ids,
          method: "post",
        }).then(({ data }) => {
          if (data && data.code === 0) {
            this.$message({
              message: "操作成功",
              type: "success",
              duration: 1500,
              onClose: () => {
                this.search();
              },
            });
          } else {
            this.$message.error(data.msg);
          }
        });
      });
    },
  },
};
</script>
