							<el-button :style='{"border":"0","cursor":"pointer","padding":"0 24px","margin":"0 10px 6px 0","outline":"none","color":"#fff","borderRadius":"30px","background":"#93C7B3","width":"auto","fontSize":"14px","height":"32px"}' v-if="isAuth('qiuzhijianli','面试邀请')" type="success" size="mini" @click="mianshiyaoqingCrossAddOrUpdateHandler(scope.row,'cross','邀请面试','','')">面试邀请</el-button>
